div.row div.react-calendar {
    border: none;
    border-radius: 10px;
  }

  .react-calendar__month-view__days__day--weekend {
    color: #000;
  }

  
  .react-calendar__tile--active {
    background-color: #695239;
  }

  .react-calendar__tile--now {
    background: none;
  }

  .react-calendar__tile--active {
    color: #000;
  }